import React from 'react';
import { bool } from 'prop-types';
import cx from 'classnames';

import './Divider.scss';

const Divider = ({fullWidth}) => {
    const cl = cx({
        'divider__container': true,
        'divider__container--fullWidth': fullWidth
    });

    return (
        <div className={cl} />
    );
}

Divider.propTypes = {
    fullWidth: bool,
}

export default Divider;



